ound-color: #fff;\n  color: #555;\n}\n\n.back-btn {\n  display: inline-block;\n  font-size: 1.6rem;\n  font-weight: 500;\n  color: #333;\n  text-decoration: none;\n  padding: 2rem 2.4rem;\n  border: none;\n  cursor: pointer;\n  // make button not fill the whole width\n  width: fit-content;\n  border-radius: 10px;\n  transition: all 0.3s;\n\n\n  &:hover {\n    background-color: #333;\n    color: #fff;\n  }\n}\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #db0000;\n\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n.loader {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background-color: #fff;\n  z-index: 100;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.loader__spinner {\n  width: 5rem;\n  height: 5rem;\n  border: 3px solid #333;\n  border-top-color: #e67e22;\n  border-radius: 50%;\n  animation: spin 1s infinite linear;\n}\n\n.empty {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-direction: column;\n  height: 100vh;\n  width: 100%;\n  text-align: center;\n\n  &__descrtipion {\n    font-size: 2rem;\n    margin-bottom: 2rem;\n  }\n}\n\n#query {\n  width: 30rem;\n  padding: 2.5rem;\n  font-size: 1.8rem;\n  border-radius: 9px;\n  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);\n  margin-top: 2rem;\n  margin-left: 2rem;\n}"],"sourceRoot":""}]);
